<template>
  <div class="container">
    <div>
      <div class="card card-muni card-muni-min">
        <h3>Iniciar Sesin</h3>
        <div class="row justify-content-center">
          <div class="col-12 col-sm-6 col-md-12">
            <div class="mt-1 line15">
              <label>Correo electrnico</label>
            </div>
          </div>
          <div class="col-12 col-sm-6 col-md-12">
            <input name="user" type="text" class="form-control" v-model="correo" />
          </div>
        </div>
        <div class="row justify-content-center mt-2">
          <div class="col-12 col-sm-6 col-md-12">
            <div class="mt-2">
              <label>Contrasea</label>
            </div>
          </div>
          <div class="col-12 col-sm-6 col-md-12">
            <input
              name="pass"
              type="password"
              class="form-control"
              v-model="contrasena"
              @keyup.enter="IniciarSesion"
            />
          </div>
        </div>
        <div class="row justify-content-center">
          <div class="col-12 col-sm-12 col-md-12 text-center mt-3">
            <button
              @click="IniciarSesion"
              name="ingresar"
              class="btn btn-block btn-lg btn-default"
            >Ingresar</button>
          </div>
        </div>

        <div class="row justify-content-center">
          <div class="col-12 col-sm-12 col-md-12">
            <hr />
          </div>
        </div>
        <div class="row justify-content-center">
          <div class="col-12 col-sm-12 col-md-12 text-center">
             An no tienes cuenta ?
            <router-link class="enlace4" to="/registroUsuarioOption">Regstrate</router-link>
          </div>
        </div>
        <div class="row justify-content-center mt-3">
          <div class="col-12 col-sm-12 col-md-12 text-center">
            Olvid la contrasea,
            <router-link class="enlace4" to="/recupera">recuperar</router-link>
          </div>
        </div>
      </div>
      <footer class="text-muted text-center mt-3">{{ version }}</footer>
    </div>
  </div>
</template>
<script>
import GlobalEventBus from "../../helpers/EventBus.js";
import axios from "axios";
import Constantes from "../../store/constantes.js";
import Util from "../../tools/util";

import { getAccesoLogin } from "../services/rentasService";
import constantes from "../../store/constantes.js";

export default {
  name: "Login",
  data() {
    return {
      version: constantes.version,
      correo: "",
      contrasena: "",
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
    };
  },
  props: {
    showModal: false
  },
  created() {
    window.rutaReturn = window.rutaReturn ? window.rutaReturn : "/";
  },
  mounted() {
    this.PaginaVista()
    localStorage.setItem("logueadoTramite", false);
  },
  methods: {
    PaginaVista(){
      dataLayer.push({
          'event': 'pagina_vista',
          'page-url': '/login', 
          'page-title': 'login'
      });
    },
    ValidaCorreoFormato(correo) {
      return correo === "" ? false : this.reg.test(correo);
    },
    ValidarFormulario() {
      if (this.correo === "") {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Ingrese cuenta de usuario"
        });
        return false;
      }
      if (this.contrasena === "") {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Ingrese su contrasea"
        });
        return false;
      }
      return true;
    },
    IniciarSesion() {
      if (this.ValidarFormulario()) {
        let PtdGenUsuarioLogin={ 'usuario': this.correo, 'contrasena': this.contrasena }
        const urlValidacion =Constantes.rutaTramite +"acceso/obtener/";
        axios.post(urlValidacion,PtdGenUsuarioLogin)
          .then(response => {
            if (response.data.success) {
              const data = response.data.data;
              localStorage.setItem("logueadoTramite", true);
              localStorage.setItem("nombreUsuarioLogueadoTramite",data.nombre);
              localStorage.setItem("paternoUsuarioLogueadoTramite",data.apePaterno);
              localStorage.setItem("maternoUsuarioLogueadoTramite",data.apeMaterno);
              localStorage.setItem("idUsuarioLogueadoTramite",data.idUsuario);
              localStorage.setItem("cuentaTrmaite", this.correo);
              localStorage.setItem("idPersonaLogueadoTramite",data.idPersona);
              localStorage.setItem("nombreCompletoLogueadoTramite",data.apePaterno +
                  " " +data.apeMaterno +" " +data.nombre);
              localStorage.setItem("numeroDocumentoLogueadoTramite", data.numeroDocumento);
              localStorage.setItem("correoNotificacionTramite", data.emailNotificacion);
              localStorage.setItem("restricciones", JSON.stringify(data.restricciones));
              Util.storage.set("usuarioMigrado", true);

              let appRentas = {};
              appRentas.contribuyente = {};
              appRentas.contribuyente.numDoc = data.numeroDocumento;
              appRentas.contribuyente.nombres = data.apePaterno + " " + data.apeMaterno + ", " + data.nombre;
              appRentas.contribuyente.email = this.correo;
              Util.storage.set("appRentas", appRentas);
              GlobalEventBus.$emit("login", data.nombre);
              this.$router.push(window.rutaReturn);
            } else if(response.data.existeUsuario) {
                if(response.data.activado){
                  this.controlErrorSesion(404);
                }else{
                  this.controlErrorSesion(405);
                }
            } else {
              let dataLogin = {};
              dataLogin.email = this.correo;
              dataLogin.pass = this.contrasena;
              let mensajeLogin = response.data.message;
              getAccesoLogin(1,dataLogin)
                .then(response => {
                  if (response.data.success) {
                    let data = response.data.data;
                    if(this.validaUsuarioRentas(data)){
                      localStorage.setItem("logueadoTramite", true);
                      localStorage.setItem("nombreUsuarioLogueadoTramite",data.nombre);
                      localStorage.setItem("paternoUsuarioLogueadoTramite",data.apePat);
                      localStorage.setItem("maternoUsuarioLogueadoTramite",data.apeMat);
                      localStorage.setItem("nombreCompletoLogueadoTramite",data.apePat + " " + data.apeMat + " " + data.nombre);
                      let nameUser = localStorage.getItem('nombreUsuarioLogueadoTramite').trim() !== ''
                        ? localStorage.getItem('nombreUsuarioLogueadoTramite')
                        : localStorage.getItem('nombreCompletoLogueadoTramite')
                      Util.storage.set("usuarioMigrado", false);

                      //Parametros necesarios para el funcionamiento de tributos
                      let appRentas = {};
                      appRentas.contribuyente = {};
                      appRentas.contribuyente.numDoc = data.numDoc.trim();
                      appRentas.contribuyente.nombres = data.apePat.trim().toUpperCase() +
                        " " + data.apeMat.trim().toUpperCase() + ", " + data.nombre.trim().toUpperCase();
                      appRentas.contribuyente.email = dataLogin.email;
                      appRentas.contribuyente.tipoDocumento = data.tipDoc;
                      appRentas.contribuyente.contribuyenteInstitucion = data.contribuyenteInstitucion;

                      Util.storage.set("appRentas", appRentas);
                      GlobalEventBus.$emit("login", nameUser);
                      this.IniciarSesion();
                    }else{
                       this.controlErrorSesion(40401);
                    }
                  } else {
                    localStorage.setItem("logueadoTramite", false);
                    this.$swal({
                      icon: "error",
                      title: "Error",
                      text:
                        mensajeLogin === "" || mensajeLogin == null
                          ? "Verifique datos"
                          : mensajeLogin
                    });
                  }
                })
                .catch(error => {
                  const codigoError = error.response.data.code;
                  this.controlErrorSesion(codigoError);
                });
            }
          })
          .catch(e => console.log("error" + e));
      }
    },
    validaUsuarioRentas(data){
      let respuesta=true;
      if(data.tipDoc!==32 && (data.nombre.trim()+ data.apePat.trim() + data.apeMat.trim()==='')){
        respuesta=false;
      }
      if(data.tipDoc===32 && (data.nombre.trim()+ data.apePat.trim()+data.apeMat.trim()==='')){
        respuesta=false;
      }
      return respuesta;
    },
    lanzaServicio(requestParams, nombreLogin) {
      if (requestParams.opcion === 0) {
        this.$router.push("/");
      } else if (requestParams.opcion === 1) {
        this.$router.push("/tramiteVirtual");
      } else if (requestParams.opcion === 2) {
        this.$router.push("/consultaTramite");
      } else if (requestParams.opcion === 5) {
        this.$router.push(
          "/tramite/ce/" +
            localStorage.getItem("areaTramite") +
            "&" +
            localStorage.getItem("tipoTramite") +
            "&" +
            localStorage.getItem("idTramite") +
            "&0"
        );
      } else if (requestParams.opcion === "pago-tributo") {
        this.$router.push("/pago-tributo");
      } else if (requestParams.opcion === "estado-cuenta") {
        this.$router.push("/estado-cuenta");
      }

      GlobalEventBus.$emit("login", nombreLogin);
    },
    controlErrorSesion(codigoError){
      if(codigoError === 404){
        this.$swal({
            icon: 'error',
            title: 'Error de inicio de sesin',
            html: "El usuario y/o la contrasea ingresados son incorrectos"
        });
      }else if(codigoError === 40401){
        let mensaje = "<div style='line-height:24px;'>Por las mejoras realizadas en nuestra plataforma on line, solo podrn ingresar ";
        mensaje += "los contribuyentes registrados, para mayor informacin comunquese al telfono ";
        mensaje += "<br/><b>(01) 617-7555</b></div>";

        this.$swal({
          icon: 'warning',
          title: 'Alerta',
          html: mensaje
        });
      }else if(codigoError === 405){
        this.$swal({
            icon: 'error',
            title: 'Error de inicio de sesin',
            html: "Debe activar su cuenta. Favor revise su correo electrnico."
        });
      }else if(codigoError === 500){
        this.$swal({
          icon: 'error',
          title: 'Error',
          text: 'Ocurrio un error inesperado'
        });
      }
    }
  }
};
</script>



