<template>
    <div class="main">
    <div class="container">
        <div class="card card-muni" :class="{ loginuser: $root.$userLogged }">
            <div class="row">
                <div class="col-6 col-md-3" v-for="(item, index) in lisItems" :key="index">
                    <div class="item">
                        <!-- <router-link :to="validaOpc(item.login, item.link)"> -->
                        <a @click.prevent="validaOpc(item.login, item.link)">    
                            <div class="content-img" >
                                <img :src="require(`../../assets/${item.icon}.png`)"/>
                            </div>
                            <h5> <b> {{item.title}}</b></h5> 
                            <p>{{item.description}}</p>
                        <!-- </router-link> -->
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <b-modal id="modalCasillaElectronica" hide-footer title="" @close="clearData">
      <div class="form-group" style="margin-bottom: 0.5rem; !important">
        <div class="row mt-2">
          <div class="col-md-12 pr-0">
            <div class="mt-12" style="text-align:center; color:#0078cf">
              <h5> <b> AUTORIZACIN DE USO DE CASILLA ELECTRNICA </b></h5>
            </div>
            <div>
                <div class="row" >
                    <div class="col-md-12" style="text-align:center;">
                        <div class="custom-control custom-checkbox my-1 mr-sm-2">
                            <input type="checkbox" class="custom-control-input" id="customControlInline" v-model="flagCasillaElectronica">
                            <label class="custom-control-label" for="customControlInline"><b>He ledo y acepto trminos y condiciones</b></label>
                        </div>
                    </div>
                </div>
                <div class="row" >
                  <div class="col-md-12" style="text-align:center; color:#0078cf">
                    <a href="javascript:;" @click="openModalCasillaElectronica"> <u> Ver Trminos y Condiciones </u></a>
                  </div>
                </div>
                <div class="row" >
                  <div class="col-md-12" style="text-align:center;">
                    <button class="btn btn-default" @click="ActualizaFlagCasillaElectronica">OK</button>
                  </div>
                </div>
            </div>
          </div>
          
          
        </div>
     
      </div>
    </b-modal>
    <b-modal id="visorModal" hide-footer title="Trminos y Condiciones" size="huge">
          <div class="contenedor-documento">
            <div class="row">
              <div style="width: 95%">
                <!-- <img src="../../images/logomuniwhite.png" class="img-drawable" id="firma" alt=""> -->
                <iframe
                  :src="rutaTerminosCasilla"
                  style="width: 100%; height: 900px"
                  type="application/pdf"
                  frameborder="0"
                ></iframe>
              </div>
            </div>

            <!-- <iframe src="../../images/ejemplo.pdf" style="width:100%; height:375px;"  type="application/pdf" frameborder="0"></iframe> -->
          </div>
        </b-modal>
    </div>
</template>
<style lang="scss">
.modal .modal-huge {
  max-width: 900px;
  width: 900px;
  max-height: 500px;
  height: 500px;
}
.modal-backdrop {
    background: #002646a8;
  }
  .modal-content {
    border: 0px;
    border-radius: 20px;
    border: solid 1px #e0e0e0;
    // box-shadow: 0px 0px 12px 3px rgba(0, 125, 197, 0.19);
  }
  .modal-body,
  .modal-header {
    box-shadow: none;
  }
</style>
<style>
.header-nav {
    background: #e9ecef;
    padding: 15px 0px;
    color: #0066cc;
}

/* .breadcrumb {
    padding-top: 20px;

}
.breadcrumb .breadcrumb {
    margin-bottom: 0;
    padding-top: 0;
} */
/* .arriba { 
    left: 0;
    position: absolute;
    text-align: center;
    top: 10;
    width: 100%;
} */
.subtitulo{
    min-height: 57px;
    position: relative;
    display: flex;
    align-items: center;
}
</style>
<script>
import Login from './Login.vue'
import Util from '../../tools/util'
import axios from "axios";
import Constantes from "../../store/constantes.js";
export default {
    components:{Login},
    data(){
        return{
            estiloMuestraLogin:'',
            estiloMuestraUsuario:'none',
            mostrarDivPreLogin:true,
            nombreUsuario:'',
            flagCasillaElectronica:false,
            rutaTerminosCasilla:Constantes.filesRecupera +'/14/1/0',
            lisItems:[
                {
                    title : 'Mesa de Partes Digital',
                    description: 'Registra tus trmites de manera virtual.',
                    link: '/tramiteVirtual',
                    icon: 'mesadepartes',
                    login: 1
                },
                {
                    title : 'Consulte su Trmite',
                    description: 'Consulta el estado de tus trmites ingresados.',
                    link: '/consultaTramite',
                    icon: 'consultatutramite',
                    login: 1
                }
                ,
                /* , */
                /*{
                    title : 'Fraccionamiento Virtual',
                    description: 'Solicita el fraccionamiento de tu deuda',
                    link: '/ingresar'
                }*/
                /* {
                    title : 'Multas Administrativas',
                    description: 'Pago de Multas Administrativas',
                    link: '/multas-administrativas',
                    icon: 'solicitud'
                /*{
                    title : 'Internamiento Vehicular',
                    description: 'Pago de Internamiento Vehicular',
                    link: '/ingresar'
                }*/
                {
                    title : 'Casilla Electrnica',
                    description: 'Revisar casilla electrnica',
                    link: '/casilla-electronica',
                    icon: 'solicitud',
                    login: 3
                },
            ]
        }
    },
    created(){
        window.rutaReturn = '/' 
    },
    mounted() {
        let appRentas = Util.storage.get("appRentas");
        if(appRentas!=null && appRentas.cuenta!=null){
            delete appRentas.cuenta;
            Util.storage.set("appRentas", appRentas);
        }
        console.log('version 3')
        this.ValidaLogueo();
    },
    methods: {
        ActualizaFlagCasillaElectronica(){
            if(this.flagCasillaElectronica){
                let usuario={}
                usuario.idUsuarioPlataforma=localStorage.getItem('idUsuarioLogueadoTramite')
                axios.post(Constantes.rutaTramite+'activaCasillaElectronica/',usuario)
                .then(response=>{
                if(response.data.success){
                    this.$router.push('/casilla-electronica');
                }else{
                    this.$swal({
                        icon: "info",
                        title: "Aviso",
                        text: "Sucedi un error al actualizar"
                    });
                }
                    
                
            })
            .catch(e=>console.log(e))
            }else{
                this.$bvModal.hide("modalCasillaElectronica");
            }
        },
        openModalCasillaElectronica(){
          this.$bvModal.show("visorModal");
        },
        ValidaLogueo(){
            var flagLogueado= localStorage.getItem('logueadoTramite'); 
            console.log('logueado:' +  flagLogueado);
            if(flagLogueado=='true'){
                console.log('entr:' +  flagLogueado);
                this.mostrarDivPreLogin=false;
                this.estiloMuestraLogin='none';
                this.estiloMuestraUsuario='';
                this.nombreUsuario=localStorage.getItem('nombreCompletoLogueadoTramite');  
            }else{
                console.log('no entr:' +  flagLogueado);
                this.mostrarDivPreLogin=true;
                this.estiloMuestraLogin='';
                this.estiloMuestraUsuario='none';
                this.nombreUsuario=localStorage.getItem('nombreCompletoLogueadoTramite');  
            }
        },
        validaOpc(opc, link){
            if(opc==3){
                this.validaCasillaElectronica(link)
            }else{
                if(opc==2 && this.$root.$userLogged==false){
                    this.$root.$userCuponera = false;
                    this.$router.push(link);
                }else{
                    this.$root.$userCuponera = true;
                    this.$router.push(link);
                }
            }
            
        },
        clearData(){

        },
        validaCasillaElectronica(link){
            axios.get(Constantes.rutaTramite+'/detalleUsuarioPlataforma/'+localStorage.getItem('idUsuarioLogueadoTramite'))
            .then(response=>{
                let data=response.data.data;
                let casillaEletronica=data[0].flagCasillaElectronica
                if(casillaEletronica==1){
                    this.$router.push(link);
                }else{
                    this.$bvModal.show("modalCasillaElectronica");
                }
            })
            .catch(e=>console.log(e))
        }
        // tramiteVirtual(){
        //     this.$router.push('/tramiteVirtual');
        // },
        // consultaTramite(){
        //     this.$router.push('/consultaTramite');
        // },
        // IngresarOpcion(){

        // }
    },
}
</script>


